<script>
import _account from "@/utils/api/account";
import Edit from '@/mixins/account-management/level-management/Edit'

export default {
  name: 'EditDirector',
  mixins:[Edit],
  data () {
    return {
      // 層級代號
      level: 6,
      // 上層層級代號
      upLevel: 7
    }
  },
  methods: {
    async init () {
      this.initLanguage()

      this.$toastr.options.timeOut = 3600000 // 一小時後自動消失
      this.$toastr.options.extendedTimeOut = 1000 // 當使用者觸碰到提示訊息時，離開後超過此設定時間則隱藏提示訊息
      this.$toastr.options.closeButton = true // 顯示關閉按鈕

      // 設定當前編輯資料
      if (this.$route.query.account) {
        let params = {
          l_lv:this.level ,
          searchAccount: this.$route.query.account
        }
        let res = await _account.getAccount(params);

        if (res.hasOwnProperty('list')) {
          this.reload = new Date().getTime() ;
          await this.setAccount(res.list)
        }

        // 拆帳百分比**在模組元件設置

        // 球類設定資訊(載入時判斷是否顯示各球類修改列表)
        for (let key in this.mapSportName) {
          // editGameSetItems 是否有 this.mapSportName[i] 這個 key
          if (this.editGameSetItems.hasOwnProperty(key)) {
            // 隱藏表
            if (this.showGameSetItems.selected.indexOf(key) === -1) this.editGameSetItems[key].show = false
            // 顯示表
            else this.editGameSetItems[key].show = true
          }
        }
        // 載入額度預設值
        this.loadQuota()
        // @API優化修改@
        this.loadGameSet()
        // @API優化修改@
        this.loadDefaultGameSet()
        /* 刷新各遊戲上層資訊 */
        this.loadUpAccountAllInfo()
      } else {
        // 無物件資料，則轉跳上一頁
        this.toBack()
      }
      this.updateDashboardInfo()
      // 直接不開放世足
      this.closeGameType('s15')
    },
    toBack () {
      this.$router.push({
        path: '/manage/account-management/director-management',
        query: {
          pageType: 'edit'
        }
      })
    },
    // 取得當前上層帳號值
    getUpAccountValue () {
      // 編輯總監，若登入大總監帳號，則送的上層帳號為「登入帳號」
      return sessionStorage.getItem('super.manage.account')
    }
  }
}
</script>

